import React from 'react'
import Helmet from 'react-helmet'
import ContactForm from '../partials/ContactForm'
import FooterInfo from '../partials/FooterInfo'

const ContactPage = () => (
  <div>
    <div className="contact-page align-center">
      <Helmet
        title="Contact - Fitato"
        meta={[
          { name: 'description', content: 'Get in touch with Fitato - available 24*7' },
        ]}
      />
      <div className="container">
        <h1>Get in Touch - Available 24*7</h1>
        <div className="contact-page-form">
          <p>We are always available at your service, if you have any further queries/questions or even need a workout partner, connect with us.</p>
          <ContactForm />
        </div>
        <ul className="footer-ul">
          <li><a href="mailto:connect@fitato.fit">connect@fitato.fit</a></li>
          <li><a href="tel:+918408999984">+91 8408999984</a></li>
          <li><a href="tel:+918390229669">+91 8390229669</a></li>
        </ul>
        <ul className="footer-social">
          <li>
            <a href="https://www.facebook.com/Fitato.fit/" target="_blank" rel="noopener">
              <img src="static/icons/facebook.png" />
            </a>
          </li>
          <li>
            <a href="https://twitter.com/fitatofit" target="_blank" rel="noopener">
              <img src="static/icons/twitter.png" />
            </a>
          </li>
          <li>
            <a href="https://plus.google.com/+FitatoFitness" target="_blank" rel="noopener">
              <img src="static/icons/googleplus.png" />
            </a>
          </li>
          <li>
            <a href="https://www.youtube.com/watch?v=pKC3-oPRudA" target="_blank" rel="noopener">
              <img src="static/icons/youtube.png" />
            </a>
          </li>
          <li>
            <a href="https://www.instagram.com/fitato_fit/" target="_blank" rel="noopener">
              <img src="static/icons/instagram.png" />
            </a>
          </li>
        </ul>
      </div>
    </div>
    <FooterInfo />
  </div>
)

export default ContactPage



